* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Urbanist", Arial, Helvetica, sans-serif;
}

body {
  background-color: #00E9DF;
  background-image: url("../images/wave.png");
  background-repeat: no-repeat;
  background-position: center;
  background-position: top;
}

.header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  height: 160px;
}
.header .header__titulo {
  font-weight: bold;
  font-size: 60px;
}
.header .header__logo {
  width: 120px;
  margin-right: 30px;
}
.header .header__menu {
  max-width: 60px;
  margin-left: 60px;
}